<template>
  <div>
    <input
      v-if="doneFill"
      readonly
      :class="{ 'read-only--bg': doneFill }"
      class="box box--large w-100"
      type="text"
      :value="field.message"
      disabled
    />
    <input
      v-else
      :readonly="doneFill"
      :class="{ 'read-only--bg': doneFill }"
      class="box box--large w-100"
      type="text"
      v-model="messageText"
      placeholder="入力してください"
      v-validate="'required'"
      name="name"
      @input="handleMessage"
      :disabled="!field.visibility"
    />
    <div class="pt-4">
      <span class="valid-error">{{ errors.first("name") }}</span>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    field: Object,
    doneFill: Boolean
  },
  data() {
    return {
      messageText: ""
    };
  },
  computed: {},

  methods: {
    handleMessage(event) {
      let value = this.messageText.trim();
      if (value != "") {
        let newField = { ...this.field };
        newField.message = value;
        this.$emit("changeMessage", newField);
      }
    }
  }
};
</script>
<style scoped>
.read-only--bg {
  background-color: #f8f8f8;
}
</style>



