<template>
  <div>
    <div class="position-relative">
      <div class="box box--large" @click="handleCategory">
        <div class="dropdown">
          <span
            class="dropdown-title"
            :class="{ 'dropdown-title--black': !openCategory }"
          >
            {{ doneFill ? field.message : value }}
            <span class="select-icon"></span>
          </span>
        </div>
      </div>
      <div class="data" v-if="openCategory">
        <ul class="data-wrap">
          <li
            class="data-item"
            v-for="(item, index) in data"
            :key="index"
            @click="changeValue(item)"
          >
            <p>{{ item.name }}</p>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    data: Array,
    field: Object,
    doneFill: Boolean
  },
  data() {
    return {
      openCategory: false,
      value: "選択してください "
    };
  },
  methods: {
    changeValue(item) {
      this.value = item.name;
      this.openCategory = false;
      let newField = { ...this.field };
      newField.message = this.value;
      this.$emit("changeMessage", newField);
    },
    handleCategory() {
      if (this.doneFill === true) {
        this.openCategory = false;
      } else this.openCategory = !this.openCategory;
    }
  }
};
</script>

<style scoped>
.dropdown {
  cursor: pointer;
}
.dropdown-title {
  display: block;
  width: 100%;
  font-size: 1.14rem;
  color: #999;
  position: relative;
}
.select-icon {
  display: block;
  width: 0;
  height: 0;
  border-left: 4px solid transparent;
  border-right: 4px solid transparent;
  border-top: 8px solid #666;
  position: absolute;
  top: 50%;
  right: 1.5rem;
  transform: translateY(-50%);
}
.data {
  padding: 0.57rem 0;
  max-height: 220px;
  height: 100%;
  width: 100%;
  overflow-y: scroll;
}
.data::-webkit-scrollbar {
  display: none;
}
.data-wrap {
  background-color: #fff;
}
.data-item {
  padding: 0.57rem 0.71rem;
  cursor: pointer;
}
.data-item:hover {
  background-color: #f1f2f7;
}
.dropdown-title--black {
  color: #333;
}
</style>



