<template>
  <div class="advance">
    <header>
      <div class="logo">
        <img src="../assets/logo.svg" class="w-100" />
      </div>
      <div class="menu">
        <ul>
          <li
            class="menu-item"
            :class="{ 'active-menu': item.isActive }"
            v-for="(item, index) in menu"
            :key="index"
          >
            <p class="menu-item__title">
              {{ item.title }}
            </p>
          </li>
        </ul>
      </div>
    </header>
    <section class="content">
      <div class="content-wrap">
        <div class="content-title">
          <div
            class="previous-link"
            v-if="currentStep > 0"
            @click="previousStep"
          >
            <div class="previous-icon">
              <img src="../assets/ArrowLeft.svg" class="w-100" />
            </div>
            <h3 class="previous-link__des">雇用契約に戻る</h3>
          </div>
          <h2>{{ steps[currentStep].title }}</h2>
        </div>
        <div class="step-icons pt-24">
          <StepIcon :currentStep="currentStep"></StepIcon>
        </div>
        <div class="calendar pt-24">
          <div class="icon">
            <img src="../assets/calendar 2.svg" class="w-100" />
          </div>
          <div class="calendar-desc">
            <p>{{ date }} までご登録ください</p>
          </div>
        </div>
        <div class="notice pt-16">
          <div class="bg--grey">
            <div class="bg--wrap">
              <div class="icon">
                <img src="../assets/frame.svg" class="w-100" />
              </div>
              <div class="notice-title title">
                <h3>ご確認ください</h3>
              </div>
              <div class="notice-content">
                <p>
                  {{ steps[currentStep].notice }}
                </p>
              </div>
            </div>
          </div>
        </div>
        <div v-if="currentStep == 0">
          <FirstStep @nextStep="nextStep"></FirstStep>
        </div>
        <div v-else-if="currentStep == 1">
          <SecondStep @nextStep="nextStep"></SecondStep>
        </div>
        <div v-else-if="currentStep == 2">
          <ThirdStep @submitForm="submitForm"></ThirdStep>
        </div>
      </div>
    </section>
  </div>
</template>
<script>
import { mapGetters } from "vuex";
import { ValidationObserver, ValidationProvider } from "vee-validate";
import StepIcon from "../components/stepIcon.vue";
import FirstStep from "../components/firstStep.vue";
import SecondStep from "../components/secondStep.vue";
import ThirdStep from "../components/thirdStep.vue";
import ForthStep from "../components/forthStep.vue";
export default {
  data() {
    return {
      currentStep: 1,
      date: "",
      menu: [
        {
          title: "入社手続き",
          isActive: true
        },
        {
          title: "ヘルプ",
          isActive: false
        }
      ],
      steps: [
        {
          title: "雇用契約について",
          notice:
            "表示されている内容を確認いただき、認識に相違が無ければ同意するにチェックをしてください。"
        },
        {
          title: "入社手続きの登録をお願いいたします",
          notice:
            "名前と生年月日が表示されている場合は正しく表示されているか確認をして、間違えている場合は修正をお願いいたします。"
        },
        {
          title: "入社手続きの確認をお願いいたします",
          notice:
            "入力した内容に誤りがある場合は「入社手続き申請」に戻り修正をおこなってください。"
        }
      ],
      errorNextStep: -1
    };
  },
  components: {
    StepIcon,
    FirstStep,
    SecondStep,
    ThirdStep,
    ForthStep,
    ValidationObserver
  },
  computed: {
    ...mapGetters({})
  },
  mounted() {
    this.date = this.getDate();
  },
  methods: {
    getDate() {
      return new Date().toLocaleDateString();
    },
    stateCheck(value) {
      this.errorNextStep = value;
    },
    nextStep() {
      this.currentStep++;
      window.scrollTo(0, 0);
    },
    previousStep() {
      this.currentStep--;
    },
    submitForm() {
      this.$emit("doneFill");
    }
  }
};
</script>

<style>
.advance {
  width: 100%;
  overflow: hidden;
}
/* header */
header {
  width: 100%;
  padding: 12px 20px;
}
.logo {
  width: 40px;
  height: 40px;
  display: inline-block;
  vertical-align: middle;
}
.menu {
  display: inline-block;
}
.menu-item {
  display: inline-block;
  padding-left: 1.71rem; /* 24px */
  cursor: pointer;
}
.menu-item__title {
  font-size: 1.14rem; /* 16px */
  color: #999;
}
.active-menu .menu-item__title {
  font-weight: 700;
  color: #333;
}
/* end header */
/* content */
.content {
  width: 100%;
  overflow: hidden;
}
.content-wrap {
  max-width: 40.12rem; /* 572px */
  width: 100%;
  padding-top: 3.43rem; /* 48px */
  margin: 0 auto;
}
.content-title h2 {
  font-size: 1.71rem;
  line-height: 1.71rem;
}
.calendar-desc,
.notice-title {
  display: inline-block;
  vertical-align: middle;
  padding-left: 0.57rem;
}
.previous-link {
  padding-bottom: 0.57rem;
  cursor: pointer;
}
.previous-icon {
  width: 16px;
  height: 16px;
  display: inline-block;
  vertical-align: middle;
  cursor: pointer;
}
.previous-link__des {
  display: inline-block;
  padding-left: 0.57rem;
  color: #666;
  font-size: 1.14rem;
  cursor: pointer;
}
.notice {
  width: 100%;
  overflow: hidden;
}
.notice-title h3 {
  font-weight: 700;
}
.notice-content {
  padding-top: 1.14rem;
}
.notice-content p {
  line-height: 1.57rem;
  color: #666;
}
.action-btn__wrap button {
  width: 100%;
  border: none;
  border-radius: 3px;
  background-color: #b2b1ff;
  text-align: center;
  color: #fff;
  font-weight: 700;
  font-size: 1.3rem;
  padding: 0.857rem 0.71rem;
  margin-bottom: 5.7rem;
  cursor: pointer;
}
</style>



