<template>
  <div id="app">
    <Home @doneFill="doneFill" v-if="!closeStep"></Home>
    <Submit v-if="nextStep === true"></Submit>
  </div>
</template>

<script>
import Home from "./pages/home.vue";
import Submit from "./pages/submit.vue";
export default {
  name: "App",
  components: {
    Home,
    Submit
  },
  data() {
    return {
      nextStep: false,
      closeStep: false
    };
  },
  methods: {
    doneFill() {
      this.nextStep = true;
      this.closeStep = true;
    }
  }
};
</script>

<style></style>



