<template>
  <div>
    <div class="pt-16">
      <div class="bg--grey">
        <div class="bg--wrap">
          <div class="title title-regular">
            <h3>住民票記載の住所を登録</h3>
          </div>
          <div class="pt-24">
            <div class="box box--large">
              <div class="checkbox">
                <input
                  type="checkbox"
                  id="checkbox"
                  @click="handleCheck"
                  :checked="addressCheck"
                />
                <label for="checkbox"
                  >同意する場合はチェックをしてください</label
                >
              </div>
            </div>
          </div>
          <div
            class="pt-24"
            v-for="(item, index) in signupAddress"
            :key="index"
          >
            <div
              class="required"
              :class="{ 'pl-0': item.mulRequire }"
              v-if="item.isRequire"
            >
              <p><span v-if="item.mulRequire">どちらか</span>必須</p>
            </div>
            <p class="required-detail color--black">
              {{ item.detail }}
            </p>
            <p class="required-detail__des pl-0">
              {{ item.des }}
            </p>
            <div class="pt-8">
              <form>
                <InputComponent
                  :field="item"
                  v-if="item.type == 'text'"
                  @changeMessage="changeMessage"
                  :doneFill="doneFill"
                ></InputComponent>
                <SelectChoose
                  v-else-if="item.type == 'choose'"
                  :field="item"
                  :doneFill="doneFill"
                  @changeRadioChoose="changeRadioChoose"
                ></SelectChoose>
                <DateComponent v-else-if="item.type == 'date'"></DateComponent>
                <Dropzone
                  v-else-if="item.type == 'dropzone'"
                  :name="item.detail"
                  :field="item"
                  @changeMessage="changeMessage"
                  :doneFill="doneFill"
                ></Dropzone>
                <SearchInput
                  v-else-if="item.type == 'search'"
                  :visibility="item.visibility"
                  :field="item"
                  :doneFill="doneFill"
                ></SearchInput>
                <Dropdown
                  v-else-if="item.type == 'dropdown'"
                  :data="data"
                  :field="item"
                  @changeMessage="changeMessage"
                  :doneFill="doneFill"
                ></Dropdown>
                <Calendar
                  v-else-if="item.type == 'calendar'"
                  :field="item"
                  @changeMessage="changeMessage"
                  :doneFill="doneFill"
                ></Calendar>
                <MultiInput
                  v-else-if="(item.type = 'multi-input')"
                  :field="item"
                  @changeMessage="changeMessage"
                  :doneFill="doneFill"
                ></MultiInput>
              </form>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import InputComponent from "./InputComponent.vue";
import SelectChoose from "./selectChoose.vue";
import DateComponent from "./dateComponent.vue";
import Dropzone from "./Dropzone.vue";
import SearchInput from "./SearchInput.vue";
import Dropdown from "./dropdown.vue";
import Calendar from "./calendar.vue";
import MultiInput from "./MultiInput.vue";
import { mapGetters } from "vuex";
export default {
  props: {
    doneFill: Boolean
  },
  components: {
    InputComponent,
    Dropdown,
    SelectChoose,
    DateComponent,
    Dropzone,
    SearchInput,
    Calendar,
    MultiInput
  },
  data() {
    return {
      check: false
    };
  },
  computed: {
    ...mapGetters({
      signupAddress: "getSignupAddress",
      data: "getData",
      addressCheck: "getAddressCheck"
    })
  },
  methods: {
    changeMessage(value) {
      this.$store.dispatch("updateAdressData", value);
    },
    handleCheck() {
      this.check = !this.check;
      if (this.check == true) {
        this.$store.dispatch("AddressCheck", true);
      } else this.$store.dispatch("AddressCheck", false);
    }
  }
};
</script>



