<template>
  <div>
    <div class="edu-input">
      <input
        v-if="doneFill"
        type="date"
        disabled
        class="w-100 box"
        :value="field.message"
        min="1990-01-01"
        max="2025-01-01"
      />
      <input
        v-else
        type="date"
        class="w-100 box"
        min="1900-01-01"
        max="2025-01-01"
        v-model="date"
        @input="handleDate"
      />
      <span class="edu-input__icon"></span>
    </div>
    <div></div>
  </div>
</template>
<script>
export default {
  props: {
    field: Object,
    doneFill: Boolean
  },
  data() {
    return {
      date: ""
    };
  },
  computed: {
    newField() {
      return { ...this.field };
    }
  },
  methods: {
    handleDate() {
      let newField = { ...this.field };
      newField.message = this.date;
      this.$emit("changeMessage", newField);
    }
  }
};
</script>

<style scoped>
.edu-input {
  max-width: 11.43rem;
  position: relative;
}
.edu-input input {
  padding: 1.14rem;
  color: #333;
  font-size: 1.14rem;
  cursor: auto;
}
.edu-input input::placeholder {
  color: #666;
}
input[type="date"]::-webkit-calendar-picker-indicator {
  position: absolute;
  width: 24px;
  height: 24px;
  display: block;
  top: 50%;
  right: 0.857rem;
  transform: translateY(-50%);
  background-image: url("../assets/calendar.svg");
  background-repeat: no-repeat;
}
</style>



