<template>
  <div>
    <div class="pt-16">
      <div class="bg--grey">
        <div class="bg--wrap">
          <div class="title title-regular">
            <h3>在留カード（外国籍の方はご記入ください）</h3>
          </div>
          <div class="pt-8">
            <div class="box box--large">
              <div class="checkbox">
                <input
                  type="checkbox"
                  id="checkbox"
                  @click="showVisibility($event)"
                  :checked="resident[0].visibility"
                  :disabled="doneFill"
                />
                <label for="checkbox"
                  >同意する場合はチェックをしてください</label
                >
              </div>
            </div>
          </div>
          <div
            class="pt-24"
            v-for="(item, index) in resident"
            :key="index"
            :class="{ 'visibility-hidden': !item.visibility }"
          >
            <div class="required" v-if="item.isRequire">
              <p>必須</p>
            </div>
            <p class="required-detail color--black">
              {{ item.detail }}
            </p>
            <p class="required-detail__des">
              {{ item.des }}
            </p>
            <div class="pt-8">
              <form>
                <InputComponent
                  :field="item"
                  v-if="item.type == 'text'"
                  @changeMessage="changeMessage"
                  :doneFill="doneFill"
                ></InputComponent>
                <SelectChoose
                  v-else-if="item.type == 'choose'"
                  :field="item"
                  :doneFill="doneFill"
                  @changeRadioChoose="changeRadioChoose"
                ></SelectChoose>
                <DateComponent v-else-if="item.type == 'date'"></DateComponent>
                <Dropzone
                  v-else-if="item.type == 'dropzone'"
                  :name="item.detail"
                  :field="item"
                  @changeMessage="changeMessage"
                  :doneFill="doneFill"
                ></Dropzone>
                <SearchInput
                  v-else-if="item.type == 'search'"
                  :visibility="item.visibility"
                  :field="item"
                  :doneFill="doneFill"
                ></SearchInput>
                <Dropdown
                  v-else-if="item.type == 'dropdown'"
                  :data="data"
                  :field="item"
                  @changeMessage="changeMessage"
                  :doneFill="doneFill"
                ></Dropdown>
                <Calendar
                  v-else-if="item.type == 'calendar'"
                  :field="item"
                  @changeMessage="changeMessage"
                  :doneFill="doneFill"
                ></Calendar>
              </form>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import InputComponent from "./InputComponent.vue";
import SelectChoose from "./selectChoose.vue";
import DateComponent from "./dateComponent.vue";
import Dropzone from "./Dropzone.vue";
import SearchInput from "./SearchInput.vue";
import Dropdown from "./dropdown.vue";
import Calendar from "./calendar.vue";
import { mapGetters } from "vuex";
export default {
  props: {
    doneFill: Boolean
  },
  data() {
    return {};
  },
  components: {
    InputComponent,
    Dropdown,
    SelectChoose,
    DateComponent,
    Dropzone,
    SearchInput,
    Calendar
  },
  computed: {
    ...mapGetters({
      resident: "getResident",
      data: "getData"
    })
  },
  methods: {
    changeMessage(value) {
      this.$store.dispatch("updateResidentData", value);
    },
    showVisibility(e) {
      if (e.target.checked) {
        this.$store.dispatch("showResidentVisibility");
      } else {
        this.$store.dispatch("hideResidentVisibility");
      }
    }
  }
};
</script>



