<template>
  <div>
    <div class="bth">
      <div class="bth-item">
        <input
          class="fill-info__input box box--large w-100"
          type="text"
          placeholder="西暦（半角）"
          v-validate="'between:1900,2022'"
          v-model="year"
          name="year"
          :disabled="doneFill"
          @input="handDate"
        />
        <label>年</label>
        <div class="pt-4 bth-error" v-if="errors.first('year')">
          <span class="valid-error">{{
            errors.first("year") ? "Valid {1900-2022}" : ""
          }}</span>
        </div>
      </div>
      <div class="bth-item">
        <input
          class="fill-info__input box box--large w-100"
          type="text"
          placeholder="半角"
          v-model="month"
          v-validate="'between:1,12'"
          name="month"
          :disabled="doneFill"
          @input="handDate"
        />
        <label>月</label>
        <div class="pt-4 bth-error">
          <span class="valid-error">{{
            errors.first("month") ? "Valid {1-12}" : ""
          }}</span>
        </div>
      </div>
      <div class="bth-item">
        <input
          class="fill-info__input box box--large w-100"
          type="text"
          placeholder="半角"
          v-model="day"
          v-validate="'between:1,31'"
          name="day"
          :disabled="doneFill"
          @input="handDate"
        />
        <label>日</label>
        <div class="pt-4 bth-error">
          <span class="valid-error">
            {{ errors.first("day") ? "Valid {1-31}" : "" }}
          </span>
        </div>
      </div>
      <div class="pt-4">
        <span class="valid-error">{{ messageError }}</span>
      </div>
    </div>
    <div class="bth-age" v-if="doneFill">
      <h3 class="title--small color--6">年齢</h3>
      <p class="pt-8">{{ age }} 歳</p>
    </div>
    <div class="pt-24" v-else>
      <h6 class="title--small">年齢</h6>
      <p class="pt-8 color--6">生年月日を入力すると表示されます</p>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    field: Object,
    doneFill: Boolean,
  },
  data() {
    return {
      year: "",
      month: "",
      day: "",
      messageError: "",
    };
  },
  computed: {
    age() {
      let yearNum = parseInt(this.year);
      let currentYear = new Date().getFullYear();
      return currentYear - yearNum;
    },
    catchError() {
      if (this.messageError != "") return true;
      else return false;
    },
  },
  methods: {
    checkError(date, month, year) {
      if (month === 2) {
        if ((year % 100 === 0 && year % 400) || year % 4 == 0) {
          if (date > 29) {
            return `February hasn't ${date} days`;
          }
        } else {
          if (date > 28) {
            return `February hasn't ${date} days`;
          }
        }
      } else if (
        month == 1 ||
        month == 3 ||
        month == 5 ||
        month == 7 ||
        month == 8 ||
        month == 10 ||
        month == 12
      ) {
        if (date > 31) return `Month: ${month} hasn't ${date} days`;
      } else {
        if (date > 30) return `Month: ${month} hasn't ${date} days`;
      }
      return "";
    },
    handDate() {
      let yearNum = parseInt(this.year);
      let monthNum = parseInt(this.month);
      let dateNum = parseInt(this.day);
      if (this.day != "" && this.month != "" && this.year != "") {
        this.messageError = this.checkError(dateNum, monthNum, yearNum);
        let tmpMonth = "";
        let tmpDate = "";
        if (monthNum < 10 && monthNum > 0) {
          tmpMonth = `0${this.month}`;
        } else tmpMonth = this.month;
        if (dateNum < 10 && dateNum > 0) {
          tmpDate = `0${this.day}`;
        } else tmpDate = this.day;
        let userBth = `${tmpMonth} ${tmpDate} ${this.year}`;
        let newField = { ...this.field };
        if (this.messageError.length === 0) {
          newField.message = userBth;
        } else newField.message = "";
        this.$emit("changeMessage", newField);
      }
    },
  },
  mounted() {
    if (this.doneFill) {
      let newMonth = this.field.message.slice(0, 2);
      this.month = newMonth;
      let newDate = this.field.message.slice(3, 5);
      this.day = newDate;
      let newYear = this.field.message.slice(6, 10);
      this.year = newYear;
    }
  },
};
</script>

<style scoped>
.bth {
  display: inline-block;
  justify-content: center;
  align-items: stretch;
}
.bth-item {
  display: inline-block;
  vertical-align: middle;
  position: relative;
}
.bth-item .fill-info__input {
  max-width: 8.3rem;
  width: 100%;
}
.bth-item label {
  font-size: 1.14rem;
  color: #666;
  padding-left: 0.57rem;
  padding-right: 1.14rem;
  font-weight: 700;
}
.bth-error {
  position: absolute;
  left: 0;
  top: 100%;
}
.bth-age {
  padding-top: 1.357rem;
}
</style>



