<template>
  <div>
    <div v-if="name == 'dropzone'">
      <div class="dropzone-img"></div>
      <div class="box">
        <div class="dropzone" :dragover="handleDrop">
          <div class="upload">
            <div class="upload-icon" @click="openFile">
              <img
                src="../assets/uploadI.png"
                class="w-100"
                alt="upload image"
              />
            </div>
            <input class="open-file" type="file" alt="upload" hidden />
            <div class="bg--wrap p-8">
              <div class="upload-des">
                <h4 class="upload-des__title">ファイルをドラッグ&ドロップ</h4>
                <p>ファイルをドロップするか、</p>
                <p>ファイルを参照する</p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div v-else-if="name == 'text'">
      <input
        class="box box--large w-100"
        type="text"
        v-model="message"
        placeholder="入力してください"
        v-validate="'required'"
        name="text"
        @keyup="handleMessage($event)"
      />
      <div class="pt-4">
        <span class="valid-error">{{ errors.first("text") }}</span>
      </div>
    </div>
    <div v-else-if="name == 'choose'">
      <div class="choose-checkbox box p-0">
        <div class="choose-checkbox-item">
          <input type="checkbox" class="choose-checkbox__check" id="男性" />
          <label class="choose-checkbox__label" for="男性">男性</label>
        </div>
        <div class="choose-checkbox-item">
          <input type="checkbox" class="choose-checkbox__check" id="女性" />
          <label class="choose-checkbox__label" for="女性">女性</label>
        </div>
      </div>
    </div>
    <div v-else-if="name == 'date'">
      <div class="bth">
        <div class="bth-item">
          <input
            class="fill-info__input box box--large w-100"
            type="text"
            placeholder="西暦（半角）"
          />
          <label>年</label>
        </div>
        <div class="bth-item">
          <input
            class="fill-info__input box box--large w-100"
            type="text"
            placeholder="半角"
          />
          <label>月</label>
        </div>
        <div class="bth-item">
          <input
            class="fill-info__input box box--large w-100"
            type="text"
            placeholder="半角"
          />
          <label>日</label>
        </div>
      </div>
    </div>
    <div v-else-if="name == 'calendar'">
      <div class="edu-input">
        <input class="w-100 box" type="text" />
        <img src="../assets/calendar.svg" class="edu-input__icon" />
      </div>
    </div>
    <div v-else-if="name == 'search'">
      <form class="input-search">
        <input
          type="text"
          class="w-100 box"
          placeholder="フリーワードで検索できます"
          :disabled="!visibility"
        />
        <div class="input-search__icon">
          <img src="../assets/MagnifyingGlass.svg" class="w-100" />
        </div>
      </form>
    </div>
    <div v-else-if="name == 'multi-input'">
      <div class="short-input">
        <input type="text" class="w-100 box box--large" placeholder="半角" />
      </div>
      <div class="short-input__img">
        <img src="../assets/ー.svg" class="w-100" />
      </div>
      <div class="short-input">
        <input type="text" class="w-100 box box--large" placeholder="半角" />
      </div>
      <div class="short-input__btn">
        <button>住所取得</button>
      </div>
    </div>
    <div v-else>
      <div class="box box--large">
        <div class="dropdown">
          <span class="dropdown-title">
            選択してください <span class="select-icon"></span>
          </span>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  props: ["name", "visibility", "field", "parent"],
  data() {
    return {
      countValid: 0,
      message: ""
    };
  },
  methods: {
    handleMessage(event) {
      if (this.message.trim() != "") {
        let newField = { ...this.field };
        newField.message = this.message;
        event.target.parentNode.parentNode.parentNode.parentNode.nextElementSibling.focus();
        this.$emit("updateData", newField);
      }
    },
    showFile(file) {
      var imgDrop = document.querySelector(".dropzone-img");
      let fileType = file.type;
      //validate type file
      let validExtersion = [
        "image/jpeg",
        "image/jpg",
        "image/png",
        "image/svg+xml"
      ];
      if (validExtersion.includes(fileType)) {
        // creating new object
        let fileReader = new FileReader();
        fileReader.onload = () => {
          let fileUrl = fileReader.result;
          let imgTag = `<img src="${fileUrl}" class="w-100">`;
          imgDrop.innerHTML = imgTag;
        };
        fileReader.readAsDataURL(file);
      } else {
        alert("This is not image");
      }
    },
    openFile() {
      var inputFile = document.querySelector(".open-file");
      let file;
      inputFile.click();
      inputFile.addEventListener("change", function() {
        file = this.files[0];
        var imgDrop = document.querySelector(".dropzone-img");
        let fileType = file.type;
        //validate type file
        let validExtersion = [
          "image/jpeg",
          "image/jpg",
          "image/png",
          "image/svg+xml"
        ];
        if (validExtersion.includes(fileType)) {
          // creating new object
          let fileReader = new FileReader();
          fileReader.onload = () => {
            let fileUrl = fileReader.result;
            let imgTag = `<img src="${fileUrl}" class="w-100">`;
            imgDrop.innerHTML = imgTag;
          };
          fileReader.readAsDataURL(file);
        } else {
          alert("This is not image");
        }
      });
    },
    handleDrop(event) {
      // getting user select file
      // [0] this means is user select multiple files then we'll select only the first one
      let file;
      file = event.dataTransfer.files[0];
      this.showFile(file);
    }
  },
  mounted() {
    const dropzone = document.querySelector(".dropzone");
    var inputFile = document.querySelector(".open-file");
    dropzone.addEventListener("dragover", event => {
      event.preventDefault();
    });

    dropzone.addEventListener("drop", event => {
      event.preventDefault();
      this.handleDrop(event);
    });
  }
};
</script>

<style scoped>
/* dropzone */
.dropzone-img {
  width: 8.57rem;
  margin: 0 auto;
  padding-bottom: 1.71rem;
}
.upload {
  text-align: center;
}
.upload-icon {
  width: 32px;
  height: 32px;
  cursor: pointer;
  margin: 0 auto;
}
.upload-des p {
  font-size: 1rem;
  line-height: 1.57rem;
}
.upload-des__title {
  font-weight: 700;
}
.choose-checkbox {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.choose-checkbox-item {
  max-width: 50%;
  width: 100%;
  position: relative;
  padding: 0.875rem;
  border-right: 1px solid #dcdcdc;
}
.choose-checkbox-item:last-child {
  border-right: none;
}
.choose-checkbox__check {
  vertical-align: middle;
  width: 16px;
  height: 16px;
  border: 1px solid #c4c4c4;
  outline: none;
  cursor: pointer;
  appearance: none;
  -webkit-appearance: none;
  border-radius: 50%;
  position: relative;
}
.choose-checkbox__check:checked {
  outline: none;
  border: none;
  background-color: #45d1c9;
  color: #fff;
}
.choose-checkbox__check::before {
  content: "";
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: scale(0);
  -moz-transform: scale(0);
  -webkit-transform: scale(0);
  -o-transform: scale(0);
  -ms-transform: scale(0);
  width: 8px;
  height: 6px;
  transition: 120ms transform ease-in-out;
  background-color: #fff;
  transform-origin: bottom left;
  clip-path: polygon(14% 44%, 0 65%, 50% 100%, 100% 16%, 80% 0%, 43% 62%);
}
.choose-checkbox__check:checked::before {
  transform: translate(-50%, -50%) scale(1);
  -moz-transform: translate(-50%, -50%) scale(1);
  -webkit-transform: translate(-50%, -50%) scale(1);
  -o-transform: translate(-50%, -50%) scale(1);
  -ms-transform: translate(-50%, -50%) scale(1);
}
.choose-checkbox__label {
  padding-left: 0.857rem;
  vertical-align: middle;
  cursor: pointer;
}
.bth {
  display: inline-block;
  justify-content: center;
  align-items: center;
}
.bth-item {
  display: inline-block;
  vertical-align: middle;
}
.bth-item .fill-info__input {
  max-width: 8.3rem;
  width: 100%;
}
.bth-item label {
  font-size: 1.14rem;
  color: #666;
  padding-left: 0.57rem;
  padding-right: 1.14rem;
  font-weight: 700;
}
.edu-input {
  max-width: 11.43rem;
  position: relative;
}
.edu-input input {
  padding: 1.57rem;
}
.edu-input__icon {
  position: absolute;
  top: 50%;
  right: 0.857rem;
  transform: translateY(-50%);
}
.dropdown {
  cursor: pointer;
}
.dropdown-title {
  display: block;
  width: 100%;
  font-size: 1.14rem;
  color: #999;
  position: relative;
}
.select-icon {
  display: block;
  width: 0;
  height: 0;
  border-left: 4px solid transparent;
  border-right: 4px solid transparent;
  border-top: 8px solid #666;
  position: absolute;
  top: 50%;
  right: 1.5rem;
  transform: translateY(-50%);
}
.input-search {
  position: relative;
}
.input-search input {
  padding-top: 1.14rem;
  padding-bottom: 1.14rem;
  padding-right: 1.14rem;
  padding-left: calc(1.14rem + 24px + 8px);
  font-size: 1.14rem;
  line-height: 1.5rem;
  color: #333;
}
.input-search input::placeholder {
  font-size: 1.14rem;
  color: #999;
}
.input-search__icon {
  width: 24px;
  height: 24px;
  position: absolute;
  top: 50%;
  left: 1.14rem;
  transform: translateY(-50%);
}
.short-input {
  max-width: 8.3rem;
  position: relative;
  display: inline-block;
  vertical-align: middle;
}
.short-input__img {
  width: 16px;
  height: 16px;
  display: inline-block;
  vertical-align: inherit;
}
.short-input__btn {
  display: inline-block;
  vertical-align: middle;
}
.short-input__btn button {
  background: #b2b1ff;
  border: 1px solid #b2b1ff;
  box-sizing: border-box;
  border-radius: 4px;
  max-width: 8.3rem;
  padding: 0.57rem 1.8rem;
  margin-left: 1.71rem;
  cursor: pointer;
  color: #fff;
  font-size: 1.14rem;
}
</style>



