<template>
  <div>
    <div class="short-input">
      <input
        type="text"
        class="w-100 box box--large"
        placeholder="半角"
        v-model="leftSide"
        :readonly="doneFill"
      />
    </div>
    <div class="short-input__img">
      <img src="../assets/ー.svg" class="w-100" />
    </div>
    <div class="short-input">
      <input
        type="text"
        class="w-100 box box--large"
        placeholder="半角"
        v-model="rightSide"
        :readonly="doneFill"
      />
    </div>
    <div
      class="short-input__btn"
      :class="{ 'btn--blue': doneFill && field.message != '' }"
    >
      <button @click.prevent="handleInput">住所取得</button>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    field: Object,
    doneFill: Boolean
  },
  data() {
    return {
      leftSide: "",
      rightSide: ""
    };
  },
  methods: {
    handleInput() {
      if (this.leftSide != "" && this.rightSide != "") {
        let newField = { ...this.field };
        newField.message = this.leftSide + " " + this.rightSide;
        this.$emit("changeMessage", newField);
      }
    }
  }
};
</script>

<style scoped>
.short-input {
  max-width: 8.3rem;
  position: relative;
  display: inline-block;
  vertical-align: middle;
}
.short-input__img {
  width: 16px;
  height: 16px;
  display: inline-block;
  vertical-align: inherit;
}
.short-input__btn {
  display: inline-block;
  vertical-align: middle;
}
.short-input__btn button {
  background: #b2b1ff;
  border: 1px solid #b2b1ff;
  box-sizing: border-box;
  border-radius: 4px;
  max-width: 8.3rem;
  padding: 0.57rem 1.8rem;
  margin-left: 1.71rem;
  cursor: pointer;
  color: #fff;
  font-size: 1.14rem;
}
.btn--blue button {
  border-color: #007bc3;
  background-color: #fff;
  color: #007bc3;
}
</style>



