<template>
  <div class="pt-16">
    <div class="bg--grey">
      <div class="bg--wrap">
        <div class="title title-regular">
          <h3>最終学歴</h3>
          <p>
            最終学歴が大学院の方は、大学を登録後に「＋学歴を追加する」から大学院を登録してください。
          </p>
        </div>
        <div class="pt-24">
          <h6 class="title--small font-bold">学歴1</h6>
        </div>
        <div class="pt-24" v-for="(item, index) in edu" :key="index">
          <div class="pt-8">
            <div class="required">
              <p>必須</p>
            </div>
            <p class="required-detail">{{ item.detail }}</p>
            <div class="pt-8 position-relative">
              <InputComponent
                :field="item"
                v-if="item.type == 'text'"
                @changeMessage="changeMessage"
                :doneFill="doneFill"
              ></InputComponent>
              <Calendar
                v-else-if="item.type == 'calendar'"
                :field="item"
                @changeMessage="changeMessage"
                :doneFill="doneFill"
              ></Calendar>
              <Dropdown
                v-else-if="item.type == 'dropdown'"
                :data="data"
                :field="item"
                @changeMessage="changeMessage"
                :doneFill="doneFill"
              ></Dropdown>
            </div>
          </div>
        </div>
        <div class="link pt-8">
          <div class="icon">
            <img class="w-100" src="../assets/Interface.svg" />
          </div>
          <a href="#"> 学歴を追加する </a>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import Calendar from "./calendar.vue";
import Dropdown from "./dropdown.vue";
import InputComponent from "./InputComponent.vue";
import { mapGetters } from "vuex";
export default {
  props: {
    doneFill: Boolean
  },
  components: {
    Calendar,
    Dropdown,
    InputComponent
  },
  computed: {
    ...mapGetters({
      edu: "getUserEdu",
      data: "getData"
    })
  },
  methods: {
    changeMessage(value) {
      this.$store.dispatch("updateCertificateData", value);
    }
  }
};
</script>
<style scoped></style>



