<template>
  <div>
    <div class="pt-16">
      <div class="bg--grey">
        <div class="bg--wrap">
          <div class="title title-regular">現住所を登録</div>
          <div
            class="pt-24"
            v-for="(item, index) in currentAddress"
            :key="index"
          >
            <div
              class="required"
              :class="{ 'pl-0': item.mulRequire }"
              v-if="item.isRequire"
            >
              <p><span v-if="item.mulRequire">どちらか</span>必須</p>
            </div>
            <p class="required-detail color--black">
              {{ item.detail }}
            </p>
            <p class="required-detail__des pl-0">
              {{ item.des }}
            </p>
            <div class="pt-8">
              <form>
                <InputComponent
                  :field="item"
                  v-if="item.type == 'text'"
                  @changeMessage="changeMessage"
                  :doneFill="doneFill"
                ></InputComponent>
                <SelectChoose
                  v-else-if="item.type == 'choose'"
                  :field="item"
                  :doneFill="doneFill"
                  @changeRadioChoose="changeRadioChoose"
                ></SelectChoose>
                <DateComponent v-else-if="item.type == 'date'"></DateComponent>
                <Dropzone
                  v-else-if="item.type == 'dropzone'"
                  :name="item.detail"
                  :field="item"
                  @changeMessage="changeMessage"
                  :doneFill="doneFill"
                ></Dropzone>
                <SearchInput
                  v-else-if="item.type == 'search'"
                  :visibility="item.visibility"
                  :field="item"
                  :doneFill="doneFill"
                ></SearchInput>
                <Dropdown
                  v-else-if="item.type == 'dropdown'"
                  :data="data"
                  :field="item"
                  @changeMessage="changeMessage"
                  :doneFill="doneFill"
                ></Dropdown>
                <Calendar
                  v-else-if="item.type == 'calendar'"
                  :field="item"
                  @changeMessage="changeMessage"
                  :doneFill="doneFill"
                ></Calendar>
                <MultiInput
                  v-else-if="(item.type = 'multi-input')"
                  :field="item"
                  @changeMessage="changeMessage"
                  :doneFill="doneFill"
                ></MultiInput>
              </form>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import InputComponent from "./InputComponent.vue";
import SelectChoose from "./selectChoose.vue";
import DateComponent from "./dateComponent.vue";
import Dropzone from "./Dropzone.vue";
import SearchInput from "./SearchInput.vue";
import Dropdown from "./dropdown.vue";
import Calendar from "./calendar.vue";
import { mapGetters } from "vuex";
import MultiInput from "./MultiInput.vue";
export default {
  props: {
    doneFill: Boolean
  },
  components: {
    InputComponent,
    Dropdown,
    SelectChoose,
    DateComponent,
    Dropzone,
    SearchInput,
    Calendar,
    MultiInput
  },
  computed: {
    ...mapGetters({
      currentAddress: "getCurrentAddress",
      data: "getData"
    })
  },
  methods: {
    changeMessage(value) {
      this.$store.dispatch("updateCurrentAdressData", value);
    }
  }
};
</script>



