<template>
  <div class="dropzone" :class="`${name}-drop`">
    <div class="dropzone-img" :class="`${name}-img`">
      <img v-if="doneFill" :src="field.message" class="w-100" />
    </div>
    <div class="box" v-if="!doneFill">
      <div class="dropzone" :dragover="handleDrop">
        <div class="upload">
          <div class="upload-icon" @click="openFile">
            <img src="../assets/uploadI.png" class="w-100" alt="upload image" />
          </div>
          <input
            class="open-file"
            type="file"
            alt="upload"
            hidden
            :class="`${name}`"
          />
          <div class="bg--wrap p-8">
            <div class="upload-des">
              <h4 class="upload-des__title">ファイルをドラッグ&ドロップ</h4>
              <p>ファイルをドロップするか、</p>
              <p>ファイルを参照する</p>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    name: String,
    field: Object,
    doneFill: Boolean
  },
  methods: {
    showFile(file) {
      var imgDrop = document.querySelector(`.${this.name}-img`);
      let fileType = file.type;
      //validate type file
      let validExtersion = [
        "image/jpeg",
        "image/jpg",
        "image/png",
        "image/svg+xml"
      ];
      if (validExtersion.includes(fileType)) {
        // creating new object
        let fileReader = new FileReader();
        fileReader.onload = () => {
          let fileUrl = fileReader.result;
          let imgTag = `<img src="${fileUrl}" class="w-100">`;
          imgDrop.innerHTML = imgTag;
        };
        fileReader.readAsDataURL(file);
      } else {
        alert("This is not image");
      }
    },
    openFile() {
      var inputFile = document.querySelector(`.${this.name}`);
      var imgDrop = document.querySelector(`.${this.name}-img`);
      let file;
      var newField = { ...this.field };
      inputFile.click();
      const _this = this;
      inputFile.onchange = function() {
        file = this.files[0];
        let fileType = file.type;
        //validate type file
        let validExtersion = [
          "image/jpeg",
          "image/jpg",
          "image/png",
          "image/svg+xml"
        ];
        if (validExtersion.includes(fileType)) {
          // creating new object
          let fileReader = new FileReader();
          fileReader.onload = () => {
            let fileUrl = fileReader.result;
            newField.message = fileUrl;
            _this.$emit("changeMessage", newField);
            let imgTag = `<img src="${fileUrl}" class="w-100">`;
            imgDrop.innerHTML = imgTag;
          };
          fileReader.readAsDataURL(file);
        } else {
          alert("This is not image");
        }
      };
    },
    handleDrop(event) {
      // getting user select file
      // [0] this means is user select multiple files then we'll select only the first one
      let file;
      file = event.dataTransfer.files[0];
      this.showFile(file);
    }
  },
  mounted() {
    const dropzone = document.querySelector(`.${this.name}-drop`);
    dropzone.addEventListener("dragover", event => {
      event.preventDefault();
    });
    dropzone.addEventListener("drop", event => {
      event.preventDefault();
      this.handleDrop(event);
    });
  }
};
</script>
<style scoped>
.dropzone-img {
  width: 8.57rem;
  margin: 0 auto;
  padding-bottom: 1.71rem;
}
.upload {
  text-align: center;
}
.upload-icon {
  width: 32px;
  height: 32px;
  cursor: pointer;
  margin: 0 auto;
}
.upload-des p {
  font-size: 1rem;
  line-height: 1.57rem;
}
.upload-des__title {
  font-weight: 700;
}
</style>



