<template>
  <div>
    <div class="pt-16">
      <div class="bg--grey">
        <div class="bg--wrap">
          <div class="title title-regular">
            <h3>給与振込口座</h3>
          </div>
          <div
            class="pt-24"
            v-for="(item, index) in salary"
            :key="index"
            :class="{ 'visibility-hidden': item.visibility }"
          >
            <div class="required" v-if="item.isRequire">
              <p>必須</p>
            </div>
            <p class="required-detail color--black">
              {{ item.detail }}
            </p>
            <p class="required-detail__des">
              {{ item.des }}
            </p>
            <div class="pt-8">
              <InputComponent
                :field="item"
                v-if="item.type == 'text'"
                @changeMessage="changeMessage"
                :doneFill="doneFill"
              ></InputComponent>
              <Calendar
                v-else-if="item.type == 'calendar'"
                :field="item"
                @changeMessage="changeMessage"
                :doneFill="doneFill"
              ></Calendar>
              <DateComponent v-else-if="item.type == 'date'"></DateComponent>
              <Dropzone
                v-else-if="item.type == 'dropzone'"
                :name="item.detail"
                :field="item"
                @changeMessage="changeMessage"
                :doneFill="doneFill"
              ></Dropzone>
              <SelectChoose
                v-else-if="item.type == 'choose'"
                :field="item"
                :doneFill="doneFill"
                @changeRadioChoose="changeRadioChoose"
              ></SelectChoose>
              <SearchInput
                v-else-if="item.type == 'search'"
                :visibility="item.visibility"
                :field="item"
                :doneFill="doneFill"
              ></SearchInput>
            </div>
          </div>
          <div class="pt-24">
            <h3 class="title--small">口座預金科目</h3>
            <h3 class="pt-8 title--small">普通</h3>
            <div class="pt-24" v-for="(item, index) in saveMoney" :key="index">
              <div class="required" v-if="item.isRequire">
                <p>必須</p>
              </div>
              <p class="required-detail color--black">
                {{ item.detail }}
              </p>
              <p class="required-detail__des">
                {{ item.des }}
              </p>
              <div class="pt-8">
                <form>
                  <InputComponent
                    :field="item"
                    v-if="item.type == 'text'"
                    @changeMessage="changeMessage"
                    :doneFill="doneFill"
                  ></InputComponent>
                  <Calendar
                    v-else-if="item.type == 'calendar'"
                    :field="item"
                    @changeMessage="changeMessage"
                    :doneFill="doneFill"
                  ></Calendar>
                  <DateComponent
                    v-else-if="item.type == 'date'"
                  ></DateComponent>
                  <Dropzone
                    v-else-if="item.type == 'dropzone'"
                    :name="item.detail"
                    :field="item"
                    @changeMessage="changeMessage"
                    :doneFill="doneFill"
                  ></Dropzone>
                  <SelectChoose
                    v-else-if="item.type == 'choose'"
                    :field="item"
                    :doneFill="doneFill"
                    @changeRadioChoose="changeRadioChoose"
                  ></SelectChoose>
                  <SearchInput
                    v-else-if="item.type == 'search'"
                    :visibility="item.visibility"
                    :field="item"
                    :doneFill="doneFill"
                  ></SearchInput>
                </form>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import InputComponent from "./InputComponent.vue";
import SelectChoose from "./selectChoose.vue";
import DateComponent from "./dateComponent.vue";
import Dropzone from "./Dropzone.vue";
import SearchInput from "./SearchInput.vue";
import Calendar from "./calendar.vue";
import { mapGetters } from "vuex";
export default {
  props: {
    doneFill: Boolean,
  },
  components: {
    InputComponent,
    SelectChoose,
    DateComponent,
    Dropzone,
    SearchInput,
    Calendar,
  },
  computed: {
    ...mapGetters({
      salary: "getSalary",
      saveMoney: "getSaveMoney",
    }),
  },
  methods: {
    changeMessage(value) {
      this.$store.dispatch("updateSaveMoneyData", value);
    },
  },
};
</script>



