<template>
  <div>
    <div class="pt-16">
      <div class="bg--grey">
        <div class="bg--wrap">
          <div class="title title-regular">
            <h3>通勤ルート</h3>
            <div class="pt-16">
              <p>経路が２つ以上ある場合は、運賃の安い方を選択してください。</p>
              <p>
                片道料金はICカード料金ではなく現金（切符）料金となります。
              </p>
            </div>
          </div>
          <div class="pt-24">
            <h3 class="title--small">
              通勤手段1
            </h3>
            <div class="pt-24" v-for="(item, index) in routeWork" :key="index">
              <div
                class="required"
                :class="{ 'pl-0': item.mulRequire }"
                v-if="item.isRequire"
              >
                <p><span v-if="item.mulRequire">どちらか</span>必須</p>
              </div>
              <p class="required-detail color--black">
                {{ item.detail }}
              </p>
              <p class="required-detail__des pl-0">
                {{ item.des }}
              </p>
              <div class="pt-8">
                <form>
                  <InputComponent
                    :field="item"
                    v-if="item.type == 'text'"
                    @changeMessage="changeMessage"
                    :doneFill="doneFill"
                  ></InputComponent>
                  <SelectChoose
                    v-else-if="item.type == 'choose'"
                    :field="item"
                    :doneFill="doneFill"
                    @changeRadioChoose="changeRadioChoose"
                  ></SelectChoose>
                  <DateComponent
                    v-else-if="item.type == 'date'"
                  ></DateComponent>
                  <Dropzone
                    v-else-if="item.type == 'dropzone'"
                    :name="item.detail"
                    :field="item"
                    @changeMessage="changeMessage"
                    :doneFill="doneFill"
                  ></Dropzone>
                  <SearchInput
                    v-else-if="item.type == 'search'"
                    :visibility="item.visibility"
                    :field="item"
                    :doneFill="doneFill"
                  ></SearchInput>
                  <Dropdown
                    v-else-if="item.type == 'dropdown'"
                    :data="data"
                    :field="item"
                    @changeMessage="changeMessage"
                    :doneFill="doneFill"
                  ></Dropdown>
                  <Calendar
                    v-else-if="item.type == 'calendar'"
                    :field="item"
                    @changeMessage="changeMessage"
                    :doneFill="doneFill"
                  ></Calendar>
                  <MultiInput
                    v-else-if="(item.type = 'multi-input')"
                    :field="item"
                    @changeMessage="changeMessage"
                    :doneFill="doneFill"
                  ></MultiInput>
                </form>
              </div>
            </div>
            <div class="link ptb-20">
              <div class="icon">
                <img class="w-100" src="../assets/Interface.svg" />
              </div>
              <a href="#">
                学歴を追加する
              </a>
            </div>
            <!-- <div class="hidden-field">
              <div
                class="pt-24"
                v-for="(item, index) in hiddenData"
                :key="index"
              >
                <div
                  class="required"
                  :class="{ 'pl-0': item.mulRequire }"
                  v-if="item.isRequire"
                >
                  <p><span v-if="item.mulRequire">どちらか</span>必須</p>
                </div>
                <p class="required-detail color--black">
                  {{ item.detail }}
                </p>
                <div class="pt-8">
                  <form>
                    <InputComponent
                      :field="item"
                      v-if="item.type == 'text'"
                      @changeMessage="changeMessage"
                      :doneFill="doneFill"
                    ></InputComponent>
                    <Dropzone
                      v-else-if="item.type == 'dropzone'"
                      :name="item.detail"
                      :field="item"
                      @changeMessage="changeMessage"
                      :doneFill="doneFill"
                    ></Dropzone>
                    <ShortInput
                      v-else-if="item.type == 'short-input'"
                      :field="item"
                    ></ShortInput>
                  </form>
                </div>
              </div>
              <div class="hidden-field__notice">
                <p>
                  通勤費/実費：生通勤の際に自家用車を利用する場合は、単価金額が自動表示されます
                </p>
              </div>
            </div> -->
            <div class="pt-8">
              <div class="box">
                <p class="font-bold color--6">合計交通費：0円</p>
              </div>
            </div>
            <div class="pt-24">
              <h3 class="title--small">備考</h3>
              <form class="pt-8">
                <textarea class="box" placeholder="入力してください"></textarea>
              </form>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import InputComponent from "./InputComponent.vue";
import SelectChoose from "./selectChoose.vue";
import DateComponent from "./dateComponent.vue";
import Dropzone from "./Dropzone.vue";
import SearchInput from "./SearchInput.vue";
import Dropdown from "./dropdown.vue";
import Calendar from "./calendar.vue";
import { mapGetters } from "vuex";
import MultiInput from "./MultiInput.vue";
import ShortInput from "./ShortInput.vue";
export default {
  props: {
    doneFill: Boolean
  },
  components: {
    InputComponent,
    Dropdown,
    SelectChoose,
    DateComponent,
    Dropzone,
    SearchInput,
    Calendar,
    MultiInput,
    ShortInput
  },
  computed: {
    ...mapGetters({
      routeWork: "getRouter",
      data: "getData",
      hiddenData: "getHiddenData"
    })
  },
  methods: {
    changeMessage() {}
  }
};
</script>
<style scoped>
.hidden-field__notice {
  padding-top: 2.3rem;
}
.hidden-field__notice p {
  font-weight: 700;
  font-size: 1rem;
  color: #666;
}
</style>



