<template>
  <div>
    <form class="input-search" v-if="!doneFill">
      <input
        type="text"
        class="w-100 box"
        placeholder="フリーワードで検索できます"
        :disabled="visibility"
        v-model="search"
        @input="handleData"
      />
      <div class="input-search__icon">
        <img src="../assets/MagnifyingGlass.svg" class="w-100" />
      </div>
    </form>
    <div class="data" v-if="!visibility">
      <ul class="data-wrap">
        <li
          class="data-item"
          v-for="(item, index) in data"
          :key="index"
          @click="chooseValue(item)"
        >
          <p>{{ item.name }}</p>
        </li>
      </ul>
    </div>
    <div class="choose-data" v-if="!visibility">
      <ul class="choose-data__wrap">
        <li
          class="choose-data__item"
          v-for="(item, index) in chooseData"
          :key="index + 'a'"
        >
          {{ item.name }}
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
import { mapGetters } from "vuex";
export default {
  props: {
    visibility: Boolean,
    field: Object,
    doneFill: Boolean,
  },
  data() {
    return {
      search: "",
    };
  },
  computed: {
    ...mapGetters({
      data: "getFillterData",
      chooseData: "getChooseData",
    }),
  },
  methods: {
    handleData() {
      this.$store.dispatch("searchData", this.search);
    },
    chooseValue(item) {
      this.$store.dispatch("chooseData", item);
      this.search = "";
    },
  },
};
</script>

<style scoped>
.input-search {
  position: relative;
}
.input-search input {
  padding-top: 1.14rem;
  padding-bottom: 1.14rem;
  padding-right: 1.14rem;
  padding-left: calc(1.14rem + 24px + 8px);
  font-size: 1.14rem;
  line-height: 1.5rem;
  color: #333;
}
.input-search input::placeholder {
  font-size: 1.14rem;
  color: #999;
}
.input-search__icon {
  width: 24px;
  height: 24px;
  position: absolute;
  top: 50%;
  left: 1.14rem;
  transform: translateY(-50%);
}
.dropdown {
  cursor: pointer;
}
.dropdown-title {
  display: block;
  width: 100%;
  font-size: 1.14rem;
  color: #999;
  position: relative;
}
.select-icon {
  display: block;
  width: 0;
  height: 0;
  border-left: 4px solid transparent;
  border-right: 4px solid transparent;
  border-top: 8px solid #666;
  position: absolute;
  top: 50%;
  right: 1.5rem;
  transform: translateY(-50%);
}
.data {
  padding: 0.57rem 0;
  max-height: 220px;
  height: 100%;
  width: 100%;
  overflow-y: scroll;
}
.data::-webkit-scrollbar {
  display: none;
}
.data-wrap {
  background-color: #fff;
}
.data-item {
  padding: 0.57rem 0.71rem;
  cursor: pointer;
}
.data-item:hover {
  background-color: #f1f2f7;
}
.dropdown-title--black {
  color: #333;
}
.choose-data {
  width: 100%;
  overflow: hidden;
}
.choose-data__wrap .choose-data__item {
  display: inline-block;
  background: #ffffff;
  border: 1px solid #999999;
  box-sizing: border-box;
  border-radius: 2.3rem;
  padding: 0.857rem 1.14rem;
  font-size: 1.14rem;
  color: #333;
  margin-right: 0.57rem;
}
</style>



