<template>
  <div>
    <div class="choose-checkbox box p-0" ref="inputLeft">
      <div class="choose-checkbox-item">
        <input
          v-if="conditionField.message != 'left' && doneFill"
          type="radio"
          class="choose-checkbox__check disable-radio"
          id="男性"
          disabled
        />
        <input
          v-else-if="conditionField.message == 'left'"
          type="radio"
          class="choose-checkbox__check disable-radio"
          id="男性"
          checked
        />
        <input
          v-else
          type="radio"
          class="choose-checkbox__check"
          id="男性"
          value="left"
          name="radio"
          @change="changeMessage('left')"
        />
        <label
          class="choose-checkbox__label"
          :class="{
            'disable-lable': conditionField.message != 'left' && doneFill
          }"
          for="男性"
          >男性</label
        >
      </div>
      <div class="choose-checkbox-item">
        <input
          v-if="conditionField.message != 'right' && doneFill"
          type="radio"
          class="choose-checkbox__check disable-radio"
          id="男性"
          disabled
        />
        <input
          v-else
          type="radio"
          class="choose-checkbox__check"
          id="女性"
          value="right"
          name="radio"
          @change="changeMessage('right')"
        />
        <label
          class="choose-checkbox__label"
          :class="{
            'disable-lable': conditionField.message != 'right' && doneFill
          }"
          for="女性"
          >女性</label
        >
      </div>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    field: Object,
    doneFill: Boolean
  },
  computed: {
    conditionField() {
      return { ...this.field };
    }
  },
  methods: {
    changeMessage(value) {
      let newField = { ...this.field };
      newField.message = value;
      this.$emit("changeRadioChoose", newField);
    }
  }
};
</script>

<style scoped>
.choose-checkbox {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.choose-checkbox-item {
  max-width: 50%;
  width: 100%;
  position: relative;
  padding: 0.875rem;
  border-right: 1px solid #dcdcdc;
}
.choose-checkbox-item:last-child {
  border-right: none;
}
.choose-checkbox__check {
  vertical-align: middle;
  width: 16px;
  height: 16px;
  border: 1px solid #000;
  cursor: pointer;
  border-radius: 50%;
  position: relative;
}
.choose-checkbox__check:checked {
  outline: none;
  border: none;
  background-color: #fff;
  color: #fff;
  width: 16px;
  height: 16px;
  border: 1px solid #45d1c9;
}

.choose-checkbox__check::before {
  content: "";
  display: block;
  position: absolute;
  width: 8px;
  height: 8px;
  top: 50%;
  left: 50%;
  border-radius: 50%;
  transform: translate(-50%, -50%);
}

.choose-checkbox__check:checked::before {
  background-color: #45d1c9;
}

.choose-checkbox__label {
  padding-left: 0.857rem;
  vertical-align: middle;
  cursor: pointer;
}

.disable-radio {
  border-color: #c4c4c4;
}
.disable-lable {
  color: #dcdcdc;
}
</style>



