<template>
  <div>
    <div class="pt-16">
      <div class="bg--grey">
        <div class="bg--wrap">
          <div class="title title-regular">
            <h3>緊急連絡先を登録</h3>
            <div class="pt-16">
              <p>
                記入優先順位
              </p>
              <p>
                ①一親等：実父母、義父母、別居の兄弟姉妹、配偶者、子
              </p>
              <p>
                ②一親等の者の勤務先
              </p>
              <p>③二親等：別居の実祖父母、義祖父母</p>
              <p>
                ④三親等：別居の叔父叔母、甥、姪。配偶者の兄弟姉妹
              </p>
              <p>⑤知人、友人等</p>
            </div>
          </div>
          <div class="pt-24">
            <h3 class="title--small">
              緊急連絡先1
            </h3>
            <div class="pt-24" v-for="(item, index) in contact" :key="index">
              <div
                class="required"
                :class="{ 'pl-0': item.mulRequire }"
                v-if="item.isRequire"
              >
                <p><span v-if="item.mulRequire">どちらか</span>必須</p>
              </div>
              <p class="required-detail color--black">
                {{ item.detail }}
              </p>
              <p class="required-detail__des pl-0">
                {{ item.des }}
              </p>
              <div class="pt-8">
                <form>
                  <InputComponent
                    :field="item"
                    v-if="item.type == 'text'"
                    @changeMessage="changeMessage"
                    :doneFill="doneFill"
                  ></InputComponent>
                  <SelectChoose
                    v-else-if="item.type == 'choose'"
                    :field="item"
                    :doneFill="doneFill"
                    @changeRadioChoose="changeRadioChoose"
                  ></SelectChoose>
                  <DateComponent
                    v-else-if="item.type == 'date'"
                  ></DateComponent>
                  <Dropzone
                    v-else-if="item.type == 'dropzone'"
                    :name="item.detail"
                    :field="item"
                    @changeMessage="changeMessage"
                    :doneFill="doneFill"
                  ></Dropzone>
                  <SearchInput
                    v-else-if="item.type == 'search'"
                    :visibility="item.visibility"
                    :field="item"
                    :doneFill="doneFill"
                  ></SearchInput>
                  <Dropdown
                    v-else-if="item.type == 'dropdown'"
                    :data="data"
                    :field="item"
                    @changeMessage="changeMessage"
                    :doneFill="doneFill"
                  ></Dropdown>
                  <Calendar
                    v-else-if="item.type == 'calendar'"
                    :field="item"
                    @changeMessage="changeMessage"
                    :doneFill="doneFill"
                  ></Calendar>
                  <MultiInput
                    v-else-if="(item.type = 'multi-input')"
                    :field="item"
                    @changeMessage="changeMessage"
                    :doneFill="doneFill"
                  ></MultiInput>
                </form>
              </div>
            </div>
          </div>
          <div class="link pt-8">
            <div class="icon">
              <img class="w-100" src="../assets/Interface.svg" />
            </div>
            <a href="#">
              学歴を追加する
            </a>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import InputComponent from "./InputComponent.vue";
import SelectChoose from "./selectChoose.vue";
import DateComponent from "./dateComponent.vue";
import Dropzone from "./Dropzone.vue";
import SearchInput from "./SearchInput.vue";
import Dropdown from "./dropdown.vue";
import Calendar from "./calendar.vue";
import { mapGetters } from "vuex";
import MultiInput from "./MultiInput.vue";
export default {
  props: {
    doneFill: Boolean
  },
  components: {
    InputComponent,
    Dropdown,
    SelectChoose,
    DateComponent,
    Dropzone,
    SearchInput,
    Calendar,
    MultiInput
  },
  computed: {
    ...mapGetters({
      contact: "getContact"
    })
  },
  methods: {
    changeMessage(value) {
      this.$store.dispatch("updateEmergencyData", value);
    }
  }
};
</script>



