<template>
  <div class="info">
    <div class="pt-16">
      <div class="bg--grey">
        <div class="bg--wrap">
          <div class="title title-regular">
            <h3>基本情報登録</h3>
            <p>
              外国式氏名が戸籍に記載されている場合、国際結婚により戸籍上の姓が外国式の姓となっている、もしくは重国籍で戸籍上の氏名が外国式の氏名となっている場合、戸籍上の綴りで入力してください。
            </p>
          </div>
          <div class="pt-24" v-for="(item, index) in info" :key="index">
            <div class="required" v-if="item.isRequired">
              <p>必須</p>
            </div>
            <p
              class="required-detail color--black"
              :class="{ 'p-0': !item.isRequired }"
            >
              {{ item.detail }}
            </p>
            <div class="pt-8">
              <InputComponent
                :field="item"
                v-if="item.type == 'text'"
                @changeMessage="changeMessage"
                :doneFill="doneFill"
              ></InputComponent>
              <SelectChoose
                v-else-if="item.type == 'choose'"
                :field="item"
                :doneFill="doneFill"
                @changeRadioChoose="changeRadioChoose"
              ></SelectChoose>
              <DateComponent
                v-else-if="item.type == 'date'"
                :field="item"
                @changeMessage="changeMessage"
                :doneFill="doneFill"
              ></DateComponent>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import InputComponent from "./InputComponent.vue";
import SelectChoose from "./selectChoose.vue";
import DateComponent from "./dateComponent.vue";
import { mapGetters } from "vuex";
export default {
  props: {
    doneFill: Boolean
  },
  components: {
    InputComponent,
    SelectChoose,
    DateComponent
  },
  computed: {
    ...mapGetters({
      info: "getUserInfo"
    })
  },
  methods: {
    changeMessage(value) {
      this.$store.dispatch("updateInfoData", value);
    },
    changeRadioChoose(value) {
      this.$store.dispatch("updateInfoData", value);
    }
  }
};
</script>
<style scoped></style>



