<template>
  <div>
    <div
      class="step-icon__bg"
      v-for="index in totalStep"
      :key="index"
      :class="{
        'active-step': currentStep + 1 === index || index < currentStep + 1,
      }"
    >
      <div class="step-icon__inner" v-if="currentStep + 1 > index">
        <img src="../assets/Vector.svg" v-if="index % 2" class="w-100" />
        <img src="../assets/PencilLineColor.svg" class="w-100" v-else />
      </div>
      <div class="step-icon__inner" v-else>
        <img src="../assets/Check.svg" v-if="index % 2" class="w-100" />
        <img src="../assets/PencilLine.svg" class="w-100" v-else />
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      totalStep: 5,
    };
  },
  props: ["currentStep"],
};
</script>
<style scoped>
.step-icon__bg {
  width: 40px;
  height: 40px;
  margin-right: 6.43rem; /* 90px */
  display: inline-block;
  background-color: #dcdcdc;
  border-radius: 50%;
  position: relative;
}
.step-icon__bg:last-child {
  margin-right: 0;
}
.step-icon__bg::after {
  content: "";
  width: 6.43rem;
  height: 1px;
  display: block;
  background-color: #dcdcdc;
  position: absolute;
  top: 50%;
  left: 100%;
  transform: translateY(-50%);
}
.step-icon__bg:last-child::after {
  display: none;
}
.step-icon__inner {
  width: 32px;
  height: 32px;
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.active-step {
  background-color: #b2b1ff;
}
</style>



