<template>
  <div class="third-step">
    <InfoComponent :doneFill="true"></InfoComponent>
    <!-- block -->
    <Certificate :doneFill="true"></Certificate>
    <!-- block -->
    <Insurance :doneFill="true"></Insurance>
    <!-- block -->
    <Payroll :doneFill="true"></Payroll>
    <!-- block -->
    <Residence :doneFill="true"></Residence>
    <!-- block -->
    <CurrentAddress :doneFill="true"></CurrentAddress>
    <!-- block -->
    <AddressResidentCard :doneFill="true"></AddressResidentCard>
    <!-- block -->
    <EmergencyContact :doneFill="true"></EmergencyContact>
    <!-- block -->
    <FamilyInfomation :doneFill="true"></FamilyInfomation>
    <!-- block -->
    <CommutingRoute :doneFill="true"></CommutingRoute>
    <!-- button -->
    <div class="pt-16">
      <div class="action-btn__wrap" @click="submitForm">
        <button>入社手続きの入力に進む</button>
      </div>
    </div>
  </div>
</template>
<script>
import FormType from "./formType.vue";
import { ValidationObserver, ValidationProvider } from "vee-validate";
import { mapGetters } from "vuex";
import InfoComponent from "./infoComponent.vue";
import Certificate from "./Certificate.vue";
import Insurance from "./ Insurance.vue";
import Payroll from "./PayrollAccount.vue";
import Residence from "./ResidenceCard.vue";
import CurrentAddress from "./CurrentAddress.vue";
import AddressResidentCard from "./addressResidentCard.vue";
import EmergencyContact from "./EmergencyContact.vue";
import FamilyInfomation from "./familyInformation.vue";
import CommutingRoute from "./CommutingRoute.vue";
export default {
  components: {
    FormType,
    ValidationObserver,
    ValidationProvider,
    InfoComponent,
    Certificate,
    Insurance,
    Payroll,
    Residence,
    CurrentAddress,
    AddressResidentCard,
    EmergencyContact,
    FamilyInfomation,
    CommutingRoute
  },
  computed: {
    ...mapGetters({
      nextStep: "isNext"
    })
  },
  methods: {
    updateData() {},
    validateBeforeSubmit() {
      this.$store.dispatch("checkNextStep");
      if (!this.nextStep) {
        alert("Please fill the form");
      }
    },
    submitForm() {
      this.$emit("submitForm");
    }
  }
};
</script>



