<template>
  <div class="first-step">
    <form @submit.prevent="">
      <div class="bg--grey">
        <div class="bg--wrap pt-4">
          <div class="title title-regular">
            <h3>雇用契約期間の定め</h3>
          </div>
          <div class="pt-8">
            <div class="box pt-8 w-100">
              <p>契約期間の定め：なし</p>
            </div>
          </div>
        </div>
      </div>
      <!-- block -->
      <div class="pt-4">
        <div class="bg--grey">
          <div class="bg--wrap">
            <div class="title title-regular">
              <h3>勤務地</h3>
            </div>
            <form class="work-location">
              <div
                class="mt-8 box"
                v-for="(item, index) in workLocation"
                :key="index"
              >
                <label>{{ item.label }}</label>
                <input
                  type="text"
                  placeholder="テキストテキストテキストテキストテキストテキスト"
                />
              </div>
            </form>
          </div>
        </div>
      </div>
      <!-- block -->
      <div class="pt-16">
        <div class="bg--grey">
          <div class="bg--wrap">
            <div class="title title-regular">
              <h3>勤務条件</h3>
            </div>
            <div class="pt-8">
              <div class="box">
                <p>勤務区分</p>
                <p>固定勤務</p>
              </div>
            </div>
            <div class="pt-8">
              <div class="box">
                <p>勤務開始時間：09:00　勤務終了時間:18:00　休憩時間：120分</p>
                <p>
                  1週間の勤務日数（1ヶ月平均）：5日　休日：土日祝日、年末年始
                </p>
              </div>
            </div>
            <div class="pt-8">
              <div class="box">
                <p>
                  所定労働時間（1日）：8時間　所定労働時間（1週間平均）：40時間
                </p>
                <p>労働時間選択区分：40時間</p>
              </div>
            </div>
          </div>
        </div>
      </div>
      <!--block -->
      <div class="pt-16">
        <div class="bg--grey">
          <div class="bg--wrap">
            <div class="title title-regular">
              <h3>報酬条件</h3>
            </div>
            <div class="box mt-8">
              <p>等級</p>
              <p>1等級</p>
            </div>
            <div class="box mt-8">
              <p>基本給：200,000円　年齢給：200,000円　職能給：200,000円</p>
            </div>
            <div class="box mt-8">
              <p>調整給：200,000円　調整給1：200,000円　調整給2：200,000円</p>
            </div>
            <div class="pt-16">
              <div class="title title-regular">
                <h3>手当</h3>
              </div>
              <div class="box mt-8">
                <p>ライフプラン手当：200,000円</p>
              </div>
              <div class="box mt-8">
                <p>
                  役職手当：200,000円　役職手当2：200,000円　役職手当3：200,000円
                </p>
              </div>
              <div class="box mt-8">
                <p>
                  資格手当：200,000円　住宅地域手当：200,000円　管理薬剤師手当：200,000円
                </p>
              </div>
              <div class="box mt-8">
                <p>
                  職務手当：200,000円　職務手当2：200,000円　職務手当3：200,000円
                </p>
              </div>
              <div class="box mt-8">
                <p>
                  ブロック手当：200,000円　フィールドマネジャー手当：200,000円
                </p>
              </div>
              <div class="box mt-8">
                <p>単身赴任手当：200,000円　社員区分手当：200,000円</p>
              </div>
              <div class="box mt-8">
                <p>合計支給額</p>
                <p>200,000円</p>
              </div>
            </div>
          </div>
        </div>
      </div>
      <!-- block -->
      <div class="pt-16">
        <div class="bg--grey">
          <div class="bg--wrap pt-8">
            <div class="title title-regular">
              <h3>賃金の締日・支払日</h3>
            </div>
            <div class="box mt-8">
              <p>賃金締日：毎月末日　賃金支払日：当月25日</p>
            </div>
          </div>
        </div>
      </div>
      <!-- block -->
      <div class="pt-16">
        <div class="bg--grey">
          <div class="bg--wrap pt-8">
            <div class="title title-regular">
              <h3>制度</h3>
            </div>
            <div class="box mt-8">
              <p>昇給：あり　賞与：あり　退職金：あり</p>
            </div>
            <div class="box mt-8">
              <p>
                健康保険：加入する　厚生年金保険：加入する　雇用保険：加入する
              </p>
            </div>
            <div class="box mt-8">
              <p>定年制：あり　再雇用制度：ある　契約の更新：あり</p>
            </div>
          </div>
        </div>
      </div>
      <!-- block -->
      <div class="pt-16">
        <div class="bg--grey">
          <div class="bg--wrap">
            <div class="title title-regular">
              <h3>誓約書</h3>
            </div>
            <div class="box mt-8">
              <p>
                １　私は アイングループ（以下 「 会社 」
                という。）の就業規則等の諸規則・
                　　諸規定を遵守し、誠実に職務に従事いたします。
              </p>
              <p>
                ２　履歴書およびその他の提出書類の記載事項は事実に相違ありません。
              </p>
              <p>３　貴社の社員としての体面を汚すような行為はいたしません。</p>
              <p>
                ４　会社 の業務
                に関連して知りえた個人情報、企業情報（取引先・患者様・
                　　お客様
                に関する情報、人事・財務・関連会社・紛争に関する情報、その他会
                　　社が秘密として指定する情報を含む。）、については、在職中はもとよりの
                　　こと、会社退職後であっても会社の許可なくして自ら使用し、又は第三者に
                　　開示するなど漏洩いたしません。
              </p>
              <p>
                ５　会社在職中、私が保管・作成・従事を命じられた会社の個人情報・諸文書・
                　　資料・帳簿等（フロッピー、ＭＯその他記録媒体の如何を問わない。）は、
                　　情報管理者の監督のもと、責任をもって保管し、第三者に譲渡・漏洩せず、
                　　会社退職時には、これら総てを会社に返還いたします。
              </p>
              <p>
                ６　職務中に知り得た第三者の個人情報につき、その情報管理者の承諾なくして
                　　自ら使用し、又は第三者に開示・漏洩いたしません。
              </p>
              <p>
                ７　故意または重大な過失により貴社に損害を及ぼしたときは、その損害につい
                　　て賠償の責任を負います。
              </p>
            </div>
            <div class="pt-16">
              <div class="required">
                <p>必須</p>
              </div>
              <div class="box mt-8">
                <div>
                  <div class="checkbox">
                    <input
                      type="checkbox"
                      id="checkbox"
                      v-model="stateCheckbox"
                      v-validate="'required'"
                      name="check"
                      @change="checkboxState($event)"
                    />
                    <label for="checkbox"
                      >同意する場合はチェックをしてください</label
                    >
                    <div class="pt-4">
                      <span class="valid-error" v-show="errors.has('check')">{{
                        errors.first("check")
                      }}</span>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <!-- block -->
      <div class="pt-16">
        <div class="bg--grey">
          <div class="chat-info">
            <div class="chat-info__img">
              <img src="../assets/chat-info.svg" class="w-100" />
            </div>
            <div class="chat-info__desc title title-regular">
              <h3>
                雇用契約や入社手続きについて ご不明点はお問い合わせください
              </h3>
            </div>
          </div>
        </div>
      </div>
      <!-- button -->
      <div class="pt-16">
        <div class="action-btn__wrap" @click="validateBeforeSubmit">
          <button>入社手続きの入力に進む</button>
        </div>
      </div>
    </form>
  </div>
</template>
<script>
import { ValidationObserver, ValidationProvider } from "vee-validate";
import { extend } from "vee-validate";
export default {
  components: {
    ValidationObserver,
    ValidationProvider
  },
  data() {
    return {
      workLocation: [
        {
          label: "事業所名"
        },
        {
          label: "事業所在地"
        },
        {
          label: "仕事内容"
        }
      ],
      stateCheckbox: null
    };
  },
  methods: {
    checkboxState(e) {
      if (e.target.checked) {
        this.$emit("stateCheck", 0);
      } else {
        this.$emit("stateCheck", 1);
      }
    },
    validateBeforeSubmit() {
      this.$validator.validateAll().then(result => {
        if (result) {
          // eslint-disable-next-line
          this.$emit("nextStep");
          return;
        }
      });
    }
  },
  mounted() {}
};
</script>

<style scoped>
.first-step {
  padding-top: 2rem;
}
.work-location label {
  display: block;
}
.work-location input[type="text"] {
  border: none;
  width: 100%;
  font-size: 1rem;
  line-height: 1.5rem;
  color: #333;
}
.work-location__item input[type="text"]::placeholder {
  color: #333;
  font-size: 1rem;
  line-height: 1.5rem;
}
.chat-info {
  padding: 1.3rem 1.14rem;
}
.chat-info__img {
  width: 60px;
  height: 60px;
  display: inline-block;
  vertical-align: middle;
}
.chat-info__desc {
  display: inline-block;
  vertical-align: middle;
  max-width: 22.3rem;
  width: 100%;
}
.chat-info__desc {
  line-height: 1.8rem;
  padding-left: 1.14rem;
}
</style>



