<template>
  <div>
    <div class="pt-16">
      <div class="bg--grey">
        <div class="bg--wrap">
          <div class="title title-regular">
            <h3>家族情報を登録</h3>
            <div class="pt-16">
              <p>
                一等親以内は同居/別居に関わらず記入
              </p>
              <p>
                二親等以上は同居、もしくは扶養義務がある場合に限り記載
              </p>
            </div>
          </div>
          <div class="pt-24">
            <h3 class="title--small">家族情報1</h3>
            <div class="pt-24" v-for="(item, index) in family" :key="index">
              <div
                class="required"
                :class="{ 'pl-0': item.mulRequire }"
                v-if="item.isRequire"
              >
                <p><span v-if="item.mulRequire">どちらか</span>必須</p>
              </div>
              <p class="required-detail color--black">
                {{ item.detail }}
              </p>
              <p class="required-detail__des pl-0">
                {{ item.des }}
              </p>
              <div class="pt-8">
                <form>
                  <InputComponent
                    :field="item"
                    v-if="item.type == 'text'"
                    @changeMessage="changeMessage"
                    :doneFill="doneFill"
                  ></InputComponent>
                  <SelectChoose
                    v-else-if="item.type == 'choose'"
                    :field="item"
                    :doneFill="doneFill"
                    @changeRadioChoose="changeRadioChoose"
                  ></SelectChoose>
                  <DateComponent
                    v-else-if="item.type == 'date'"
                  ></DateComponent>
                  <Dropzone
                    v-else-if="item.type == 'dropzone'"
                    :name="item.detail"
                    :field="item"
                    @changeMessage="changeMessage"
                    :doneFill="doneFill"
                  ></Dropzone>
                  <SearchInput
                    v-else-if="item.type == 'search'"
                    :visibility="item.visibility"
                    :field="item"
                    :doneFill="doneFill"
                  ></SearchInput>
                  <Dropdown
                    v-else-if="item.type == 'dropdown'"
                    :data="data"
                    :field="item"
                    @changeMessage="changeMessage"
                    :doneFill="doneFill"
                  ></Dropdown>
                  <Calendar
                    v-else-if="item.type == 'calendar'"
                    :field="item"
                    @changeMessage="changeMessage"
                    :doneFill="doneFill"
                  ></Calendar>
                  <MultiInput
                    v-else-if="(item.type = 'multi-input')"
                    :field="item"
                    @changeMessage="changeMessage"
                    :doneFill="doneFill"
                  ></MultiInput>
                </form>
              </div>
            </div>
          </div>
          <div class="pt-24">
            <h3 class="title--small">
              年齢
            </h3>
            <div class="pt-8">
              <p class="color--6">生年月日を入力すると表示されます</p>
            </div>
            <div class="pt-24">
              <h3 class="title--small">家族情報1</h3>
              <div class="pt-24" v-for="(item, index) in partner" :key="index">
                <div
                  class="required"
                  :class="{ 'pl-0': item.mulRequire }"
                  v-if="item.isRequire"
                >
                  <p><span v-if="item.mulRequire">どちらか</span>必須</p>
                </div>
                <p class="required-detail color--black">
                  {{ item.detail }}
                </p>
                <p class="required-detail__des">
                  {{ item.des }}
                </p>
                <div class="pt-8">
                  <form>
                    <InputComponent
                      :field="item"
                      v-if="item.type == 'text'"
                      @changeMessage="changeMessage"
                      :doneFill="doneFill"
                    ></InputComponent>
                    <SelectChoose
                      v-else-if="item.type == 'choose'"
                      :field="item"
                      :doneFill="doneFill"
                      @changeRadioChoose="changeRadioChoose"
                    ></SelectChoose>
                    <DateComponent
                      v-else-if="item.type == 'date'"
                    ></DateComponent>
                    <Dropzone
                      v-else-if="item.type == 'dropzone'"
                      :name="item.detail"
                      :field="item"
                      @changeMessage="changeMessage"
                      :doneFill="doneFill"
                    ></Dropzone>
                    <SearchInput
                      v-else-if="item.type == 'search'"
                      :visibility="item.visibility"
                      :field="item"
                      :doneFill="doneFill"
                    ></SearchInput>
                    <Dropdown
                      v-else-if="item.type == 'dropdown'"
                      :data="data"
                      :field="item"
                      @changeMessage="changeMessage"
                      :doneFill="doneFill"
                    ></Dropdown>
                    <Calendar
                      v-else-if="item.type == 'calendar'"
                      :field="item"
                      @changeMessage="changeMessage"
                      :doneFill="doneFill"
                    ></Calendar>
                    <MultiInput
                      v-else-if="(item.type = 'multi-input')"
                      :field="item"
                      @changeMessage="changeMessage"
                      :doneFill="doneFill"
                    ></MultiInput>
                  </form>
                </div>
              </div>
              <div class="link pt-8">
                <div class="icon">
                  <img class="w-100" src="../assets/Interface.svg" />
                </div>
                <a href="#">
                  学歴を追加する
                </a>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import InputComponent from "./InputComponent.vue";
import SelectChoose from "./selectChoose.vue";
import DateComponent from "./dateComponent.vue";
import Dropzone from "./Dropzone.vue";
import SearchInput from "./SearchInput.vue";
import Dropdown from "./dropdown.vue";
import Calendar from "./calendar.vue";
import { mapGetters } from "vuex";
import MultiInput from "./MultiInput.vue";
export default {
  props: {
    doneFill: Boolean
  },
  components: {
    InputComponent,
    Dropdown,
    SelectChoose,
    DateComponent,
    Dropzone,
    SearchInput,
    Calendar,
    MultiInput
  },
  computed: {
    ...mapGetters({
      family: "getFamily",
      partner: "getPartner",
      data: "getData"
    })
  },
  methods: {
    changeMessage(value) {
      if (value.parrent == "family") {
        this.$store.dispatch("updateFamilyData", value);
      } else this.$store.dispatch("updatePartnerData", value);
    },
    changeRadioChoose(value) {
      this.$store.dispatch("updateFamilyData", value);
    }
  }
};
</script>



