<template>
  <div>
    <input
      class="box box--large short-input w-100"
      type="text"
      v-model="messageText"
      placeholder="入力してください"
      v-validate="'required'"
      name="name"
    />
    <label class="short-input__label">{{ field.des }}</label>
  </div>
</template>
<script>
export default {
  props: {
    field: Object
  },
  data() {
    return {
      messageText: ""
    };
  }
};
</script>

<style scoped>
.short-input {
  max-width: 11.5rem;
}
.short-input__label {
  padding-left: 0.857rem;
}
</style>



