<template>
  <div>
    <!-- block -->
    <div class="pt-16">
      <div class="bg--grey">
        <div class="bg--wrap">
          <div class="title title-regular">
            <h3>ご確認ください</h3>
            <p>
              システムの本人アイコンに使用します。あなたの顔が分かる写真を登録してください。未登録の場合、姓名の頭文字が表示されます。顔写真見本のように撮影してください。また、背景は白、スーツ着用、身だしなみルールに沿って撮影をお願いいたします。
            </p>
          </div>
          <div class="pt-24">
            <Dropzone :name="'avatar'"></Dropzone>
          </div>
        </div>
      </div>
    </div>
    <!-- block -->
    <div class="pt-16">
      <div class="bg--grey">
        <div class="bg--wrap">
          <div class="title title-regular">
            <h3>本人確認書類</h3>
          </div>
          <div class="pt-24">
            <div class="required">
              <p>必須</p>
            </div>
            <p class="required-detail">
              運転免許証両面もしくは住民票を添付してください。
            </p>
          </div>
          <div class="pt-8">
            <Dropzone :name="'driverCard'"></Dropzone>
          </div>
        </div>
      </div>
    </div>
    <!-- block info -->
    <InfoComponent></InfoComponent>
    <!-- block -->
    <Certificate></Certificate>
    <!-- block -->
    <Insurance></Insurance>
    <!-- block -->
    <Payroll></Payroll>
    <!-- block -->
    <Residence></Residence>
    <!-- block -->
    <CurrentAddress></CurrentAddress>
    <!-- block -->
    <AddressResidentCard></AddressResidentCard>
    <!-- block -->
    <EmergencyContact></EmergencyContact>
    <!-- block -->
    <FamilyInfomation></FamilyInfomation>
    <!-- block -->
    <CommutingRoute></CommutingRoute>
    <!-- block -->
    <div class="pt-16">
      <div class="bg--grey">
        <div class="bg--wrap">
          <div class="title title-regular">
            <h3>自家用車通勤について</h3>
          </div>
          <div class="pt-24">
            <div class="required">
              <p>必須</p>
            </div>
            <p class="required-detail color--black">
              自家用車の通勤許可を申請しますか？
            </p>
            <div class="pt-8">
              <form>
                <FormType :name="'choose'"></FormType>
              </form>
            </div>
          </div>
        </div>
      </div>
    </div>
    <!-- block -->
    <div class="pt-16">
      <div class="bg--grey">
        <div class="bg--wrap">
          <div class="title title-regular">
            <h3>自家用車の業務使用許可について</h3>
          </div>
          <div class="pt-24">
            <div class="required">
              <p>必須</p>
            </div>
            <p class="required-detail color--black">
              自家用車の通勤許可を申請しますか？
            </p>
            <div class="pt-8">
              <form>
                <FormType :name="'choose'"></FormType>
              </form>
            </div>
          </div>
        </div>
      </div>
    </div>
    <!-- block -->
    <div class="pt-24">
      <p class="color--6">入力ありがとうございました。</p>
      <p class="color--6">続けて扶養控除申告の入力をお願いいたします。</p>
    </div>
    <!-- button -->
    <div class="pt-16">
      <div class="action-btn__wrap" @click="validateBeforeSubmit">
        <button>入社手続きの入力に進む</button>
      </div>
    </div>
  </div>
</template>
<script>
import FormType from "./formType.vue";
import { ValidationObserver, ValidationProvider } from "vee-validate";
import { mapGetters } from "vuex";
import InfoComponent from "./infoComponent.vue";
import Certificate from "./Certificate.vue";
import Insurance from "./ Insurance.vue";
import Payroll from "./PayrollAccount.vue";
import Residence from "./ResidenceCard.vue";
import CurrentAddress from "./CurrentAddress.vue";
import AddressResidentCard from "./addressResidentCard.vue";
import EmergencyContact from "./EmergencyContact.vue";
import FamilyInfomation from "./familyInformation.vue";
import CommutingRoute from "./CommutingRoute.vue";
import Dropzone from "./Dropzone.vue";
export default {
  components: {
    FormType,
    ValidationObserver,
    ValidationProvider,
    InfoComponent,
    Certificate,
    Insurance,
    Payroll,
    Residence,
    CurrentAddress,
    AddressResidentCard,
    EmergencyContact,
    FamilyInfomation,
    CommutingRoute,
    Dropzone
  },
  computed: {
    ...mapGetters({
      nextStep: "isNext"
    })
  },
  methods: {
    updateData() {},
    validateBeforeSubmit() {
      this.$store.dispatch("checkNextStep");
      if (!this.nextStep) {
        alert("Please fill the form");
      } else this.$emit("nextStep", 2);
    }
  }
};
</script>
<style scoped></style>



